.form-modal__content {
  h2 {
    margin: 0 auto 8px;
    text-align: center;
    color: $color-bright;

    @include vp-1200 {
      margin-bottom: 12px;
    }

    @include vp-767 {
      margin-bottom: 6px;
    }
  }

  & > p {
    color: $color-dark;
    text-align: center;
    opacity: 1;
    margin-bottom: 18px;

    @include vp-1200 {
      margin-bottom: 22px;
    }

    @include vp-767 {
      margin: 0 auto 20px;
      max-width: 80%;
      font-size: 13px;
      letter-spacing: 0.1px;
    }
  }
}

.form-modal__form-input-container {
  i {
    color: $color-dark;
    margin-bottom: 8px;

    @include vp-767 {
      font-size: 14px;
    }
  }

  textarea {
    border: 1px solid rgba(72, 72, 72, 0.3);

    &:not(:disabled):hover,
    &:not(:disabled):focus-visible,
    &:not(:disabled):active {
      border: 1px solid $color-dark;
      color: $color-dark;
    }

    &:disabled {
      border: 1px solid rgba(169, 175, 183, 0.5);
      background-color: rgba(169, 175, 183, 0.2);
    }

    &:disabled ~ i {
      opacity: 0.6;
    }
  }
}

.form-modal__text-input-wrapper {

  label {
    @include vp-1200 {
      margin-bottom: 16px;
    }

    @include vp-767 {
      margin-bottom: 14px;
    }
  }

  input {
    border: 1px solid rgba(72, 72, 72, 0.3);

    &:not(:disabled):hover,
    &:not(:disabled):focus-visible,
    &:not(:disabled):active {
      border: 1px solid $color-dark;
      color: $color-dark;
    }

    &:disabled {
      border: 1px solid rgba(169, 175, 183, 0.5);
      background-color: rgba(169, 175, 183, 0.2);
    }

    &:disabled ~ i {
      opacity: 0.6;
    }

    @include vp-767 {
      padding: 9px 25px;
    }
  }
}

.form-modal__select {
  @include vp-1200 {
    margin-bottom: 21px;
  }

  @include vp-767 {
    margin-bottom: 15px;
  }
}

.form-modal__checkbox-wrapper {
  margin-bottom: 30px;

  @include vp-767 {
    margin-bottom: 25px;
  }

  input {
    border: solid 1px $color-bright;
    cursor: pointer;

    &:checked {
      background: $color-bright;
    }

    &:checked::before {
      background-image: url("../img/svg/checkbox-white.svg");
    }

    &:checked:not(:disabled):hover,
    &:checked:not(:disabled):focus-visible {
      background: $color-transparent;
      border: solid 1px $color-bright;

      &::before {
        background-image: url("../img/svg/checkbox.svg");
      }
    }

    &:not(:disabled):hover,
    &:not(:disabled):focus-visible {
      border: solid 1px $color-dark-bright;
    }

    &:disabled {
      border: solid 1px $color-disabled;
      background-color: $color-default-white;
      opacity: 0.6;
    }

    &:checked:disabled {
      background: $color-disabled;
      box-shadow: none;

      &::before {
        background-image: url("../img/svg/checkbox-white.svg");
      }
    }
  }

  p {
    color: $color-dark;
  }

  i {
    @include vp-767 {
      font-size: 11px;
      margin-bottom: 0;
    }
  }
}

.form-modal__button {
  max-width: fit-content;
  margin: 0 auto;

  @include vp-1200 {
    min-width: 194px;
    justify-content: space-between;
  }

  @include vp-767 {
    margin: 0;
    min-width: 100%;
  }

  span:first-child {
    width: 100%;
  }
}

.is-invalid input,
.is-invalid textarea {
  border: 1px solid #ff5e66;
  background-color: rgba(255, 94, 102, 0.2);
}
