.page-wrapper {
  position: relative;

  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));

  .header,
  .footer {
    flex-shrink: 0;
  }

  main {
    flex-grow: 1;
  }

  &--menu-opened main::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 7;
  }

  &--menu-opened {
    height: 100vh;
    position: relative;
    overflow-y: hidden;
  }

  &--menu-opened .header__logo {
    @include vp-767 {
      display: none;
    }
  }
}

.section-title {
  margin: 0;
  padding: 0;
  color: $color-bright;
  font-family: $font-regular;
  font-size: 49px;
  font-style: normal;
  font-weight: 400;
  line-height: 114%;
  letter-spacing: -1.2px;

  @include vp-1200 {
    font-size: 34px;
  }

  @include vp-767 {
    font-size: 20px;
    letter-spacing: -0.5px;
  }
}

.rouble {
  font-family: $ff-rouble;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;

  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;

  white-space: nowrap;

  border: 0;

  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;

  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;

  &::-webkit-scrollbar {
    display: none;
  }
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}
